import React, { useState, useEffect } from 'react';
import logo from './icons/logo.png';
import useClasses from './style';
import Modal from '../../components/Modal';
import firstTimer from './icons/firstTimer.png'
import {
  Box,
  Button,
  Typography,
} from '@material-ui/core'
import { useHistory, useParams } from 'react-router-dom';
import auth from 'API-Service/auth';
import { useToasts } from 'react-toast-notifications';

function MailVerification() {
  const classes = useClasses();
  const params = useParams();
  const { addToast } = useToasts();
  const [open, setOpen] = useState(false);

  const [emailVerification, setEmailVerification] = useState(true);
  const [verified, setVerified] = useState(false);
  const history = useHistory()

  setTimeout(() => {
    setEmailVerification(false)
    setVerified(true)
  }, 2000)
  useEffect(() => {
    if (params.token) {
      auth
      .verify(params.token)
      .then(response => {
        console.log(response)
        if (response.status === 200) {
          addToast(response.message, { appearance: 'success', autoDismissTimeout: 1000, autoDismiss: true })
          setTimeout(function(){history.push('/login');}, 1000);
        } else {
          addToast(response.message, { appearance: 'error', autoDismissTimeout: 5000, autoDismiss: true })
        }
      })
    }
  }, [params])
  return (
    <Box className={classes.root} style={{height: "auto"}}>
      {emailVerification && <Box className={classes.card}>
        <Box 
          component="img"
          alt="logo"
          src={logo}
        />
        <Typography className={classes.headline}>
          Great! Now verify your email
        </Typography>
        <Typography className={classes.paragraph1}>
          Please check your mailbox to verify you email <b>exampleexample@mail.com.</b> The verification link will expire in one hour
        </Typography>
      </Box>}
      {
        verified &&
        <Box className={classes.card}>
          <Typography className={classes.headline} style={{marginBottom: "1rem"}}>
            We are verified you!
          </Typography>
          <Box 
            component="img"
            alt="logo"
            src={logo}
          />
        </Box>
      }

      {open && <Modal
        open={open}
        handleClose={() => setOpen(false)}
      >
        <Box style={{paddingBottom: '1rem'}}>
          <Box>
            <Typography className={classes.headline}> 
              Wlecome To Clutch Wallet! 
            </Typography>
            <Typography className={classes.modalParagraph1}>
              A seamless experience that inspires,
              educates, and propels you into the future of finance. Please, let us know before start. Are you:
            </Typography>
          </Box>

          <Box 
            style={{}}
            className={classes.userType}
          >
            <Box>
              <Box
                component="img"
                alt="first timer"
                src={firstTimer}
              />
              <Typography className={classes.paragraph1}
                style={{paddingTop: ".7rem"}}
              >
                First timer
              </Typography>
            </Box>
            <Box>
              <Box
                className={classes.expert}
              />
              <Typography className={classes.paragraph1}
                style={{paddingTop: ".7rem"}}
              >
                Expert
              </Typography>
            </Box>
            
          </Box>
          
          <Button fullWidth
                className={classes.goBtn}
                style={{
                  textTransform: "capitalize",
                }}
              >
                Go!
          </Button>
        </Box>
      </Modal>}
    </Box>
  )
}

export default MailVerification